import React from "react";
import App from "./components/App";
import "./index.css";
import { createRoot } from "react-dom/client";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import { UserProvider } from "./context/user";

// Import all your components
import Home from "./pages/Home";
import Signup from "./pages/Signup";
import Signin from "./pages/Signin";
import MyPets from "./pages/MyPets";
import PetList from "./pages/PetList";
import NewPetForm from "./pages/NewPetForm";
import PetSightings from "./components/PetSightings";
import SinglePet from "./components/SinglePet";
import PetUpdateForm from "./components/PetUpdateForm";
import Messages from "./pages/Messages";
import Conversation from "./components/Conversation";
import NavBar from "./components/NavBar";

// Create router with proper basename
const router = createBrowserRouter([
  {
    path: "/",
    element: (
      <main>
        <NavBar/>
        <Home />
      </main>
    ),
  },
  {
    path: "/signup",
    element: (
      <main>
        <NavBar/>
        <Signup />
      </main>
    ),
  },
  {
    path: "/signin",
    element: (
      <main>
        <NavBar/>
        <Signin />
      </main>
    ),
  },
  {
    path: "/mypets",
    element: (
      <main>
        <NavBar/>
        <MyPets />
      </main>
    ),
  },
  {
    path: "/pets",
    element: (
      <main>
        <NavBar/>
        <PetList />
      </main>
    ),
  },
  {
    path: "/pets/new",
    element: (
      <main>
        <NavBar/>
        <NewPetForm />
      </main>
    ),
  },
  {
    path: "/pets/:id/sightings",
    element: (
      <main>
        <NavBar/>
        <PetSightings />
      </main>
    ),
  },
  {
    path: "/pets/:id",
    element: (
      <main>
        <NavBar/>
        <SinglePet />
      </main>
    ),
  },
  {
    path: "/pets/:id/edit",
    element: (
      <main>
        <NavBar/>
        <PetUpdateForm />
      </main>
    ),
  },
  {
    path: "/messages",
    element: (
      <main>
        <NavBar/>
        <Messages />
      </main>
    ),
  },
  {
    path: "/conversation/:otherId",
    element: (
      <main>
        <NavBar/>
        <Conversation />
      </main>
    ),
  },
], {
  basename: "/Pet-Finder"
});

const container = document.getElementById("root");
const root = createRoot(container);
root.render(
  <UserProvider>
    <RouterProvider router={router} />
  </UserProvider>
);
